.search-engine-preview {
  width: 100%;
  display: flex;
  margin-top: 70px;
  justify-content: center;

  .search-app {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-color: #111;
    box-shadow: inset 0 0 40px 1px #0b0b0b;
    color: #aaa;
    padding: 20px;
    padding-top: 28px;
    box-sizing: border-box;
    border-radius: 20px;

    .search-body {
      width: 100%;
      height: 100%;

      .start-pointer-position {
        position: absolute;
        top: 50%;
        left: 50%;
      }

      .logo-wrapper {
        width: 100%;
        height: 90px;
        padding: 10px;
        box-sizing: border-box;

        .logo {
          width: 100%;
          height: 100%;
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;

          background-image: url("../../assets/images/logo-details-search-gradient.png");
        }
      }
    }

    .tempo-input,
    .vocal-input {
      width: 48%;
      height: 85px;
      display: inline-block;
      font-size: 15px;

      & > div {
        & > div {
          width: 100%;
          text-align: center;
        }
      }
    }

    .vocal-input::after {
      content: "";
      display: block;
      clear: both;
    }

    .tempo-input {
      margin-right: 5px;

      .speed-box {
        width: 100%;
        height: 70px;
        position: relative;
        display: flex;
        justify-content: center;

        .speed {
          width: 32px;
          height: 32px;

          & > div {
            .border {
              width: 100%;
              height: 100%;
              box-sizing: border-box;
              border-radius: 50%;
              background-color: #f7463d;
            }

            .pointer-box {
              background-color: #212121;
              position: absolute;
              bottom: -1px;
              width: 90%;
              height: 90%;
              border-radius: 50% 50% 10% 10%;
              display: flex;
              justify-content: center;
              align-items: center;

              .pointer {
                width: 3px;
                height: 50%;
                background: #f5443e;
                border-radius: 2px;
              }
            }
          }
        }
      }
    }

    .sounds-input {
      & > div {
        width: 100%;
      }

      .sounds {
        width: 100%;
        height: 100px;
        display: flex;
        flex-wrap: wrap;
      }
    }

    .select-mood {
      overflow: hidden;
      position: relative;
      align-items: flex-start;

      &._focus {
        animation-delay: 3s;
        animation: select-mood-expand 1s 1s;
        animation-fill-mode: forwards;
      }

      @keyframes select-mood-expand {
        0% {
          height: 32px;
        }
        100% {
          height: 118px;
        }
      }

      .search-moods {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 1px;

        .mood {
          flex-grow: 1;
          background: #0a0a0a;
          border-radius: 8px;
          padding: 0 8px;
          margin-right: 3px;
          margin-bottom: 5px;
          color: black;
          font-weight: bold;
          text-transform: uppercase;
          font-family: "Bebas Neue", cursive;
          font-size: 15px;
        }
      }
    }
  }
}

.tempo-vocal-box {
  display: flex;
}
.card-part {
  margin-bottom: 15px;
  .card-header {
    text-align: left;
    font-family: "Bebas Neue";
    font-size: 21px;
    letter-spacing: 1px;
    color: #c8c8c8;
  }

  .card-buttons {
    display: flex;
    flex-wrap: wrap;

    max-height: 120px;
    overflow: hidden;
    justify-content: flex-start;
    margin: 5px 0;

    &.fragment {
      width: 150%;
    }
  }

  .card-badge {
  }
}
